import { Lang } from "@/types/Lang";
import { Object } from "@/types/Object";

const MONTHS = {
  ru: [
    "январь",
    "февраль",
    "март",
    "апрель",
    "май",
    "июнь",
    "июль",
    "август",
    "сентябрь",
    "октябрь",
    "ноябрь",
    "декабрь",
  ],
  tr: [
    "ocak",
    "şubat",
    "mart",
    "nisan",
    "mayıs",
    "haziran",
    "temmuz",
    "ağustos",
    "eylül",
    "ekim",
    "kasım",
    "aralık",
  ],
};

const getReadableDateOfCompletion = (
  dateOfCompletion: Object["dateOfCompletion"],
  language: string
) => {
  const date = new Date(dateOfCompletion);
  const year = date.getFullYear();
  const months = MONTHS[language as Lang];
  const month = months[date.getMonth()];

  return `${month} ${year}`.toUpperCase();
};

export default getReadableDateOfCompletion;
