
import { SelectedPriceRange } from "@/types/DataOfFiltres";
import { Object } from "@/types/Object";
import { PropType, defineComponent } from "vue";

export default defineComponent({
  name: "FilterPrice",
  props: {
    currencies: {
      type: Array as PropType<Array<Object["currency"]>>,
      required: true,
    },
    selectedPriceRange: {
      type: Object as PropType<SelectedPriceRange>,
      required: true,
    },
  },
  emits: ["update:selectedPriceRange"],
  setup(props, { emit }) {
    const updateSelectedPriceRange = (key: string, value: string) => {
      emit("update:selectedPriceRange", {
        ...props.selectedPriceRange,
        [key]: value,
      });
    };

    return {
      updateSelectedPriceRange,
    };
  },
});
