
import { Objects } from "@/types/Objects";
import { computed } from "@vue/reactivity";
import { PropType, defineComponent } from "vue";
import { Pie } from "vue-chartjs";
import { Chart as ChartJS, ArcElement, Tooltip, Legend } from "chart.js";
import compareByNumber from "@/utils/compareByNumber";
import getColors from "@/utils/getColors";
ChartJS.register(ArcElement, Tooltip, Legend);

export default defineComponent({
  name: "StatisticsDistricts",
  components: {
    Pie,
  },
  props: {
    nonRepeatingObjects: {
      type: Array as PropType<Objects>,
      required: true,
    },
  },
  setup(props) {
    const distrcitsStatisticsData = computed(() => {
      const allDistricts = props.nonRepeatingObjects.map(
        (item) => item.district
      );

      const districtCounts: { [key: string]: string | number } = {};

      allDistricts.forEach((district) => {
        districtCounts[district] = districtCounts[district]
          ? (districtCounts[district] as number) + 1
          : 1;
      });

      const districtPercentages = Object.keys(districtCounts).map(
        (district) => ({
          district,
          percentage:
            ((districtCounts[district] as number) / allDistricts.length) * 100,
        })
      );

      let totalPercentage = 0;

      if (districtPercentages.length > 0) {
        districtPercentages.forEach((item) => {
          item!.percentage = Number(item.percentage.toFixed(2));
          totalPercentage += item.percentage;
        });

        if (totalPercentage !== 100) {
          const difference = 100 - totalPercentage;
          const maxIndex = districtPercentages.reduce(
            (maxIndex, item, index, array) =>
              item.percentage > array[maxIndex].percentage ? index : maxIndex,
            0
          );
          districtPercentages[maxIndex].percentage += difference;
        }
      }

      const labels = [
        ...new Set(props.nonRepeatingObjects.map((item) => item.district)),
      ];
      const data = labels.map(
        (label) =>
          props.nonRepeatingObjects.filter((item) => item.district === label)
            .length
      );
      return {
        districtPercentages: districtPercentages
          .sort(compareByNumber("percentage"))
          .reverse(),
        chartData: {
          labels,
          datasets: [
            {
              backgroundColor: getColors(labels.length),
              data,
            },
          ],
        },
      };
    });
    return {
      distrcitsStatisticsData,
    };
  },
});
