
import { useVueFuse } from "vue-fuse";
import FilterForm from "@/components/FilterForm.vue";
import FoundObjects from "@/components/FoundObjects.vue";
import { Ref, computed, defineComponent, inject, ref, watch } from "vue";
import sortFilteredObjects from "@/utils/sortFilteredObjects";
import getFilteredObjects from "@/utils/getFilteredObjects";
import getDataOfFiltres from "@/utils/getDataOfFiltres";
import { Objects } from "@/types/Objects";
import { TypeOfObjects } from "@/types/TypeOfObjects";
import { DataOfFiltres } from "@/types/DataOfFiltres";
import { SortBy } from "@/types/SortBy.js";
import { TranslationKey, Translation } from "@/types/Translation";
import { Lang } from "@/types/Lang";
import getDataFromUser from "@/utils/getDataFromUser";
import HeaderComponent from "@/components/HeaderComponent.vue";
import getObjects from "@/api/getObjects";
import getConvertibleCurrencies from "@/api/getConvertibleCurrencies";
import { currenciesOfObjects } from "@/types/CurrenciesObjects";
import { useI18n } from "vue-i18n";
import useToast from "@/composables/useToast";

export default defineComponent({
  components: { FilterForm, FoundObjects, HeaderComponent },
  setup() {
    const objects: Ref<Objects> = ref([]); // массив обьектов, которые берутся с сервера
    const mainObjects: Ref<Objects> = ref([]);
    const dataOfFiltres: Ref<DataOfFiltres> = ref({
      selectedDistricts: [],
      selectedLayouts: [],
      selectedPriceRange: { currency: "none", from: 0, to: 0 },
      selectedDateOfCompletionRange: { from: "2000-01", to: "2100-01" },
    }); // обект с параметрами и значениями данных для фильтрации objects
    const sortBy: Ref<SortBy> = ref(""); // дополнительная фильтрация массива по алфавиту и числам
    const TRANSLATION = inject(TranslationKey) as Translation; // обьект, содержащий перевод текстов на турецкий и русский язык
    const filteredObjects: Ref<Objects> = ref([]);
    const firstMeeting = ref(true);
    const typeOfObjects: TypeOfObjects = localStorage.typeOfObjects
      ? localStorage.typeOfObjects
      : "I";
    const { locale } = useI18n();
    const TOAST = useToast();

    locale.value =
      localStorage.getItem("language") !== null
        ? (localStorage.getItem("language") as Lang)
        : "ru";

    const currenciesOfObjects: Ref<currenciesOfObjects> = ref({});
    const {
      search,
      results: resultsSearch,
      noResults: noResultsSearch,
    } = useVueFuse(filteredObjects, {
      keys: ["name"],
      threshold: 0.3,
      minMatchCharLength: 1,
    });

    const districts = computed(() => {
      return [...new Set(objects.value.map((item) => item.district))].sort();
    });
    const layouts = computed(() => {
      return [...new Set(objects.value.map((item) => item.layout))].sort();
    });
    const currencies = computed(() => {
      return [...new Set(objects.value.map((item) => item.currency))];
    });

    watch(resultsSearch, (newResults) => {
      sortBy.value = "";
      if (newResults.length > 0) {
        mainObjects.value = [...newResults];
        mainObjects.value = sortFilteredObjects({
          objects: mainObjects.value,
          by: sortBy.value,
        });
      } else if (noResultsSearch.value === true) {
        mainObjects.value = [];
      } else {
        mainObjects.value = [...filteredObjects.value];
      }
    });

    const setUserData = () => {
      const userData = getDataFromUser(true);
      if (userData === false) {
        TOAST.error();
      } else {
        TOAST.success();
      }
    };

    const changeTypeOfObjects = (type: TypeOfObjects) => {
      localStorage.setItem("typeOfObjects", type);
      location.reload();
    };
    const changeLanguage = (text: Lang) => {
      locale.value = text;
      localStorage.setItem("language", locale.value);
    };

    const updateSortBy = (by: SortBy) => {
      sortBy.value = by;
      mainObjects.value = sortFilteredObjects({
        objects: mainObjects.value,
        by: sortBy.value,
      });
    };

    getObjects(typeOfObjects).then((result) => {
      objects.value = result as Objects;
      getConvertibleCurrencies(objects.value, currencies.value).then(
        (result) => {
          currenciesOfObjects.value = result as currenciesOfObjects;
        }
      );
      dataOfFiltres.value = getDataOfFiltres(objects.value);
    });

    const findObjects = () => {
      new Promise((resolve, reject) => {
        try {
          firstMeeting.value && (firstMeeting.value = false);
          mainObjects.value = getFilteredObjects({
            objects: objects.value,
            dataOfFiltres: dataOfFiltres.value,
          });
          mainObjects.value = sortFilteredObjects({
            objects: mainObjects.value,
            by: sortBy.value,
          });
          filteredObjects.value = mainObjects.value;
          resolve(true);
        } catch (error) {
          reject(error);
        }
      })
        .then(() => {
          window.scrollTo(0, 560);
        })
        .catch((error) => {
          console.error(error);
        });
    };

    return {
      objects,
      firstMeeting,
      filteredObjects,
      setUserData,
      mainObjects,
      findObjects,
      noResultsSearch,
      search,
      resultsSearch,
      TRANSLATION,
      changeLanguage,
      dataOfFiltres,
      sortBy,
      currenciesOfObjects,
      updateSortBy,
      districts,
      layouts,
      currencies,
      typeOfObjects,
      changeTypeOfObjects,
    };
  },
});
