
import { Objects } from "@/types/Objects";
import formatterOfNumber from "@/utils/formatterOfNumber";
import { PropType, defineComponent, ref } from "vue";
import SortByElements from "./SortByElements.vue";
import { SortBy } from "@/types/SortBy";
import { currenciesOfObjects } from "@/types/CurrenciesObjects";
import MainTableObjectsMoreButton from "./MainTableObjectsMoreButton.vue";
import MainTableObjectsName from "./MainTableObjectsName.vue";

export default defineComponent({
  name: "MainTableObjects",
  emits: ["update:sortBy"],
  components: {
    SortByElements,
    MainTableObjectsMoreButton,
    MainTableObjectsName,
  },
  props: {
    objects: {
      type: Array as PropType<Objects>,
      required: true,
    },
    sortBy: {
      type: String as PropType<SortBy>,
      required: true,
    },
    currenciesOfObjects: {
      type: Object as PropType<currenciesOfObjects>,
      required: true,
    },
  },
  setup() {
    const IdForIframeTag = ref(0);
    const updateIdForIframeTag = (id: number) => {
      IdForIframeTag.value = id;
    };
    return {
      IdForIframeTag,
      formatterOfNumber,
      updateIdForIframeTag,
    };
  },
});
