
import { Object } from "@/types/Object";
import { PropType, Ref, defineComponent, ref } from "vue";
import AccordionActions from "./AccordionActions.vue";
import FieldTitleAndValue from "./FieldTitleAndValue.vue";
import formatterOfNumber from "@/utils/formatterOfNumber";
import getReadableDateOfCompletion from "@/utils/getReadableDateOfCompletion";
import { currenciesOfObjects } from "@/types/CurrenciesObjects";
import getGoogleDriveLink from "@/utils/getGoogleDriveLink";
import { useI18n } from "vue-i18n";
import { TypeOfObjects } from "@/types/TypeOfObjects";

export default defineComponent({
  name: "ModalWindow",
  components: {
    AccordionActions,
    FieldTitleAndValue,
  },
  props: {
    object: {
      type: Object as PropType<Object>,
      required: true,
    },
    index: {
      type: Number,
      required: true,
    },
    currenciesOfObjects: {
      type: Object as PropType<currenciesOfObjects>,
      required: true,
    },
    IdForIframeTag: {
      type: Number,
      required: true,
    },
  },
  setup() {
    const { locale } = useI18n();
    const typeOfObjects: TypeOfObjects = localStorage.typeOfObjects
      ? localStorage.typeOfObjects
      : "I";
    const edit: Ref<Object> = ref({
      name: "",
      link: "",
      district: "",
      coordinates: [0, 0],
      layout: "",
      dateOfCompletion: "",
      currency: "",
      price: 0,
    });
    const setEditParameters = (object: Object) => {
      edit.value = {
        ...object,
        link: getGoogleDriveLink(object.link),
      };
    };
    return {
      formatterOfNumber,
      getReadableDateOfCompletion,
      edit,
      setEditParameters,
      getGoogleDriveLink,
      locale,
      typeOfObjects,
    };
  },
});
