
import { PropType, defineComponent } from "vue";
import ThreeDotsIcon from "./Icons/ThreeDotsIcon.vue";
import ModalWindowObject from "./ModalWindowObject.vue";
import { currenciesOfObjects } from "@/types/CurrenciesObjects";
import { Object } from "@/types/Object";

export default defineComponent({
  name: "MainTableObjectsMoreButton",
  emits: ["update:IdForIframeTag"],
  props: {
    index: {
      type: Number,
      required: true,
    },
    object: {
      type: Object as PropType<Object>,
      required: true,
    },
    currenciesOfObjects: {
      type: Object as PropType<currenciesOfObjects>,
      required: true,
    },
    IdForIframeTag: {
      type: Number,
      required: true,
    },
  },
  components: { ThreeDotsIcon, ModalWindowObject },
});
