
import { Object } from "@/types/Object";
import { PropType, defineComponent, ref } from "vue";
import sendToTelegram from "@/api/sendToTelegram";
import useToast from "@/composables/useToast";

export default defineComponent({
  name: "AccordionActionChange",
  emits: ["setEditParams", "updateEditParams"],
  props: {
    object: {
      type: Object as PropType<Object>,
      required: true,
    },
    index: {
      type: Number,
      required: true,
    },
  },
  setup(props) {
    const TOAST = useToast();
    const reason = ref("");
    const disableButton = ref(false);
    const deleteObject = () => {
      disableButton.value = true;
      sendToTelegram({
        type: "delete",
        obj_1: props.object,
        reason: reason.value,
      })
        .then((response) => {
          if (response?.data.ok) {
            TOAST.success();
            disableButton.value = false;
          }
        })
        .catch((err) => {
          console.error(err);
          TOAST.error();
          disableButton.value = false;
        });
    };
    return {
      reason,
      disableButton,
      deleteObject,
    };
  },
});
