
import { Object } from "@/types/Object";
import { PropType, computed, defineComponent } from "vue";

export default defineComponent({
  name: "FilterDistricts",
  props: {
    districts: {
      type: Array as PropType<Array<Object["district"]>>,
      required: true,
    },
    selectedDistricts: {
      type: Array as PropType<Array<Object["district"]>>,
      required: true,
    },
  },
  emits: ["update:selectedDistricts"],
  setup(props, { emit }) {
    const selectedDistrictsModelValue = computed({
      get: () => {
        return props.selectedDistricts;
      },
      set(value) {
        emit("update:selectedDistricts", value);
      },
    });

    return {
      selectedDistrictsModelValue,
    };
  },
});
