
import { PropType, defineComponent } from "vue";
import ModalWindowStatistics from "./ModalWindowStatistics.vue";
import { Objects } from "@/types/Objects";
import { currenciesOfObjects } from "@/types/CurrenciesObjects";
import StatisticsIcon from "./Icons/StatisticsIcon.vue";
import { useI18n } from "vue-i18n";
import { TypeOfObjects } from "@/types/TypeOfObjects";

export default defineComponent({
  name: "HeaderComponent",
  emits: ["update:language", "update:typeOfObjects", "setUserData"],
  props: {
    objects: {
      type: Array as PropType<Objects>,
      required: true,
    },
    currenciesOfObjects: {
      type: Object as PropType<currenciesOfObjects>,
      required: true,
    },
    typeOfObjects: {
      type: String as PropType<TypeOfObjects>,
      required: true,
    },
  },
  components: { ModalWindowStatistics, StatisticsIcon },
  setup() {
    const { locale } = useI18n();

    return { locale };
  },
});
