
import { Object } from "@/types/Object";
import { computed, defineComponent, PropType } from "vue";

export default defineComponent({
  name: "FilterLayouts",
  props: {
    layouts: {
      type: Array as PropType<Array<Object["layout"]>>,
      required: true,
    },
    selectedLayouts: {
      type: Array as PropType<Array<Object["layout"]>>,
      required: true,
    },
  },
  emits: ["update:selectedLayouts"],
  setup(props, { emit }) {
    const selectedLayoutsModelValue = computed({
      get: () => {
        return props.selectedLayouts;
      },
      set(value) {
        emit("update:selectedLayouts", value);
      },
    });
    return {
      selectedLayoutsModelValue,
    };
  },
});
