import axios from "axios";
import { currenciesOfObjects } from "@/types/CurrenciesObjects";
import { Objects } from "@/types/Objects";

const getConvertibleCurrencies = async (
  objects: Objects,
  currencies: string[]
) => {
  const currenciesOfObjects: currenciesOfObjects = {};
  try {
    for (const currency of currencies) {
      if (currenciesOfObjects[currency] === undefined) {
        const response = await axios(
          `https://cdn.jsdelivr.net/gh/fawazahmed0/currency-api@1/latest/currencies/${currency.toLowerCase()}/rub.json`
        );
        currenciesOfObjects[currency] = response.data.rub;
      }
    }
    return currenciesOfObjects;
  } catch (error) {
    console.error(error);
  }
};

export default getConvertibleCurrencies;
