
import { PropType, computed, defineComponent } from "vue";
import { Objects } from "@/types/Objects";
import { currenciesOfObjects } from "@/types/CurrenciesObjects";
import StatisticsNonRepeatingObjects from "./StatisticsNonRepeatingObjects.vue";
import StatisticsAverageByLayouts from "./StatisticsAverageByLayouts.vue";
import StatisticsCurrencyExchangeRate from "./StatisticsCurrencyExchangeRate.vue";
import StatisticsAverageByDistricts from "./StatisticsAverageByDistricts.vue";
import StatisticsDistricts from "./StatisticsDistricts.vue";

export default defineComponent({
  name: "ModalWindowStatistics",
  components: {
    StatisticsNonRepeatingObjects,
    StatisticsAverageByLayouts,
    StatisticsCurrencyExchangeRate,
    StatisticsAverageByDistricts,
    StatisticsDistricts,
  },
  props: {
    objects: {
      type: Array as PropType<Objects>,
      required: true,
    },
    currenciesOfObjects: {
      type: Object as PropType<currenciesOfObjects>,
      required: true,
    },
  },
  setup(props) {
    const nonRepeatingObjects = computed(() => {
      return [
        ...new Map(props.objects.map((item) => [item.name, item])).values(),
      ];
    });

    return {
      nonRepeatingObjects,
    };
  },
});
