
import { Objects } from "@/types/Objects";
import { PropType, defineComponent, ref } from "vue";
import { useVueFuse } from "vue-fuse";
import InputSearchByObjects from "./InputSearchByObjects.vue";
import NothingWasFound from "./NothingWasFound.vue";

export default defineComponent({
  name: "StatisticsNonRepeatingObjects",
  components: { InputSearchByObjects, NothingWasFound },
  props: {
    nonRepeatingObjects: {
      type: Array as PropType<Objects>,
      required: true,
    },
  },
  setup(props) {
    const viewObjects = ref(false);
    const {
      search,
      results: resultsSearch,
      noResults: noResultsSearch,
    } = useVueFuse(props.nonRepeatingObjects, {
      keys: ["name"],
      threshold: 0.3,
      minMatchCharLength: 1,
    });
    return {
      viewObjects,
      search,
      resultsSearch,
      noResultsSearch,
    };
  },
});
