
import { PropType, defineComponent } from "vue";
import compareAlphabetically from "@/utils/compareAlphabetically";
import { computed } from "@vue/reactivity";
import { Objects } from "@/types/Objects";
import formatterOfNumber from "@/utils/formatterOfNumber";

export default defineComponent({
  name: "StatisticsAverageByLayouts",
  props: {
    objects: {
      type: Array as PropType<Objects>,
      required: true,
    },
  },
  setup(props) {
    const averageObjects = computed(() => {
      const grouped = props.objects.reduce((acc: any, item) => {
        const currency = item.currency;
        const layout = item.layout;
        if (!acc[currency]) {
          acc[currency] = {
            sum: {},
            count: {},
            averages: [],
          };
        }
        if (!acc[currency].sum[layout]) {
          acc[currency].sum[layout] = 0;
        }
        if (!acc[currency].count[layout]) {
          acc[currency].count[layout] = 0;
        }
        acc[currency].sum[layout] += item.price;
        acc[currency].count[layout]++;
        return acc;
      }, {});

      const averageObjects = Object.entries(grouped).map(([currency, data]) => {
        const sum = (data as any).sum;
        const count = (data as any).count;
        const averages = (data as any).averages;
        const layouts = Object.keys(sum);
        layouts.forEach((layout) => {
          const avg = sum[layout] / count[layout];
          averages.push({
            layout,
            average: Math.round(avg),
          });
        });
        return {
          currency,
          averages: averages.sort(compareAlphabetically("layout")),
        };
      });

      return averageObjects;
    });
    return {
      averageObjects,
      formatterOfNumber,
    };
  },
});
