import { createI18n } from "vue-i18n";
import ru from "./languages/ru";
import tr from "./languages/tr";

const messages = {
  ru: ru,
  tr: tr,
};

// 2. Create i18n instance with options
const i18n = createI18n({
  legacy: false,
  locale: "ru",
  fallbackLocale: "tr",
  messages,
});

export default i18n;
