import { toast } from "vue3-toastify";
import { useI18n } from "vue-i18n";

const useToast = () => {
  const { t } = useI18n();
  const TOAST = {
    error: () =>
      toast.error(t("message.ERROR_HAS_OCCURED"), {
        autoClose: 3000,
        position: toast.POSITION.BOTTOM_RIGHT,
      }),
    success: () =>
      toast.success(t("message.SUCCESS"), {
        autoClose: 3000,
        position: toast.POSITION.BOTTOM_RIGHT,
      }),
  };

  return TOAST;
};

export default useToast;
